.blog-item{
    box-sizing: border-box;
    width: calc(50% - 10px);
    border-radius: 8px;
    margin-bottom: 12px;
    overflow: hidden;
    box-shadow: 5px 6px 14px rgba(0,0,0,0.20);
    .image-thumb{
        cursor: pointer;
        width: 100%;
        height: 200px;
        object-fit: cover;
    }
    .content-detail{
        padding: 16px;
        .title{
            margin: 0;
            font-size: 20px;
            font-weight: bold;
            color: #313131;
            &:hover{
                cursor: pointer;
            }
        }
        .author{
            margin: 0;
            margin-top: 4px;
            font-size: 12px;
            color: grey;
        }
        .body{
            margin: 0;
            margin-top: 18px;
            font-size: 16px;
            color: #353535;
            &:hover{
                cursor: pointer;
            }
        }
        .viewBlog{
            
            padding: 5px;
            border-radius: 2px;
            background-color: #1414db;
            outline: none;
            border: none;
            color: white;
            &:hover{
                cursor: pointer;
            }
        }
    }
}